.text-overflow {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.close-Icon {
  color: #e8e8e8;
  font-size: 12px;
  -webkit-transition: color 0.3s;
  transition: color 0.3s;
  cursor: pointer;
}
.close-Icon:hover {
  color: rgba(0, 0, 0, 0.335);
}
.RA-header {
  color: #ffffff;
  height: 60px;
  background: var(--component-background);
  position: relative;
  box-sizing: border-box;
  text-align: left;
  box-shadow: 4px 4px 40px 0 rgba(0, 0, 0, 0.05);
  z-index: 2;
  display: flex;
  align-items: center;
  -webkit-transition: width 0.3s;
  transition: width 0.3s;
  width: 100%;
  flex: 0 0 auto;
}
.RA-header.RA-header-isMobile {
  left: 0;
  width: 100% !important;
}
.RA-header .RA-header-foldIcon {
  font-size: 18px;
  margin-left: 32px;
  color: #666;
  cursor: pointer;
}
.RA-header .RA-header-rightPlace {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  height: 100%;
  position: absolute;
  right: 0;
  top: 0;
  min-width: 180px;
}
.RA-header.RA-header-vertical.RA-header-darkTheme {
  box-shadow: 0px 4px 40px 0 #000000 !important;
}
.RA-header.RA-header-vertical.RA-header-inlineLayout {
  left: 0 !important;
  justify-content: unset;
  box-shadow: 4px 4px 6px 0 rgba(0, 0, 0, 0.05);
  z-index: 4;
}
.RA-header.RA-header-vertical.RA-header-inlineLayout.RA-header-fixHeader {
  position: fixed;
  width: 100%;
  top: 0;
  right: 0;
}
.RA-header.RA-header-vertical.RA-header-splitLayout.RA-header-withoutMenu {
  width: 100% !important;
}
.RA-header.RA-header-vertical.RA-header-splitLayout.RA-header-fixHeader {
  position: fixed;
  width: calc(100% - 255px);
  top: 0;
  right: 0;
}
.RA-header.RA-header-vertical.RA-header-splitLayout.RA-header-fixHeader.RA-header-collapsed {
  width: calc(100% - 80px);
}
.RA-header.RA-header-horizontal {
  left: 0px !important;
}
.RA-header.RA-header-horizontal > .RA-header-container {
  max-width: 1250px;
  margin: auto;
  position: relative;
  display: flex;
  flex: 1 1;
  max-height: 60px;
  width: 100%;
}
.RA-header.RA-header-horizontal > .RA-header-container.RA-header-container-flowMode {
  max-width: unset;
}
.RA-header.RA-header-horizontal > .RA-header-container .RA-header-rightPlace {
  position: relative;
}
.RA-header.RA-header-horizontal > .RA-header-container .RA-header-headerNav {
  flex: 1 1;
  min-width: 0;
}
.RA-header.RA-header-horizontal > .RA-header-container .siteDetail {
  justify-self: start;
}
